.vacationContainerBackground {
    width: 90%;
    min-height: 450px;
    height: 100%;
    overflow-y: hidden;
    border: 1px black solid;
    background-position: center !important;
    background-size: cover !important;
    color: white !important;
    text-shadow: 1px 2px 2px black;
}

.vacationContainer {
    margin-bottom: 15px !important;
}

.logoutButton {
    margin-right: 10px;
}

.followerBackground {
    background-color: rgba(245, 222, 179, 0.651);
    width: 150px;
    height: 150px;
    border-radius: 150px;
}

.followerBackground>p {
    top: 35px;
    position: relative;
}

.vacationDescription>h6 {
    word-wrap: break-word;
    width: 400px;
}